.statistics{
     flex-direction: column;
  align-items: center;
  padding: 15px;
  margin-top: 50px;
  margin-bottom: 30px;
  margin-left: auto;
  margin-right: auto;
  width: 400px;
  background-color: whitesmoke;
  border: 1px solid black;
}

.title {
    text-align: center;
        margin: 0 0 15px 0;
        text-transform: uppercase;
        color: #757575;
}

.statList{
list-style-type: none;
    display: flex;
    width: 100%;
    padding: 0;
    margin: 0;
    width: 100%;
}

.item{
display: flex;
    flex-direction: column;
    align-items: center;
    width: 20%;
    padding: 20px;
}

.label {
    font-size: 18px;
}

.percentage {
    font-size: 24px;
}